<template>
   <div class="container">
     <div class="test">
       <h1>Latest Posts</h1>
       <!-- Create Post -->
       <input type="text" v-model="text" name="text" ref="tt">
        <button v-on:click="postData">Submit</button>

       <hr>
       <p class="error" v-if="error">{{error}}</p>
       <div class="post-container">
         <div class="post" v-for="(post,index) in posts" v-bind:key="post._id" v-on:dblclick="deleteData(post._id)">
           {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}`}}
           <p class="text">{{post.text}}</p>
         </div>
       </div>

     </div>
   </div>
  
</template>

<script>

import PostService from '../postService';

export default {
  name: 'PostComponent',
  data () {
    return {
      posts : [],
      text : '',
      error : ''
    }
  },
  async created(){
    try{
      
      const posts = await PostService.getPosts();
      this.posts = posts;
    }catch(err){
      this.error = err.message;
    }
  },
  methods : {
    async postData(){
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
      
    },
     async deleteData(id){
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
   .post{
     background: lightgray;
    padding: 7px;
    border-radius: 5px;
    width: 500px;
    margin: 0 auto;
    margin-bottom: 5px;
   }

   .error{
     color : red;
   }

</style>



